import React from 'react'
import TarjetaHeader from './TarjetaHeader'
import TarjetaFooter from './TarjetaFooter'
import TarjetaImagen from './TarjetaImagen'
import TarjetaDatos from './TarjetaDatos'

const Tarjeta = (props) => {
    return (
        <div className="card mb-4 box-shadow">            
            <TarjetaHeader tipo={props.tipo}/>

            <TarjetaImagen poster={props.imagen} />

            <TarjetaDatos nombre={props.nombre} rating={props.rating} votos={props.votos} meta={props.meta} />

            <TarjetaFooter />
        </div>
    )
}

export default Tarjeta