import React from 'react'
import Pelicula from './Pelicula'

 const PeliculasList = (props) => {
    return (
        <div className="album py-5 bg-light">
            <div className="container">       
                <div className="row">
                {
                    props.peliculas.map( peli => (
                        <Pelicula 
                            key={peli.id}
                            id={peli.id}
                            nombre={peli.nombre}
                            imagen={peli.imagen}
                            rating={peli.rating}
                            votos={peli.votos}
                            meta={peli.metascore}
                            tipo={peli.tipo}
                        />
                    ))
                }
                </div>      
            </div>
        </div>
    )
 }

export default PeliculasList