import React from 'react'

const TarjetaDatos = (props) => (
        <ul className="list-group list-group-flush">
            <li className="list-group-item">{props.nombre}</li>
            <li className="list-group-item">Rating: {props.rating}</li>
            <li className="list-group-item">Votos: {props.votos}</li>
            <li className="list-group-item">Critica: {props.meta}</li>
        </ul>
    )

export default TarjetaDatos