import React from 'react'
import Tarjeta from './Tarjeta'

const Pelicula = (props) => {
    return (
        <div className="col-md-2">
            <Tarjeta 
                imagen={props.imagen} 
                tipo={props.tipo} 
                nombre={props.nombre} 
                meta={props.meta} 
                rating={props.rating} 
                votos={props.votos} 
                />
        </div>
    )
}

export default Pelicula