import React from 'react'
import uid from 'uid'

const PeliculaForm = (props) => {
    if(props.enablecalcular === true){
        return (
            <section className="py-3">
                <div className="container">
                    <form onSubmit={props.onAddPelicula} className="form-inline">
                        <div className="form-group">
                        <input type="text" className="form-control form-control-sm" placeholder="Nombre de la pelicula" name="titulo" required></input>
                        </div>
                        <input type="hidden" name="id" value={uid(10)}></input>                    
                        <div className="form-group">
                        <input type="submit" className="ml-2 btn btn-primary btn-sm" value="aceptar"></input>
                        </div>
                        <div className="form-group">
                        <input type="button" onClick={props.onCalcular} className="ml-2 btn btn-success btn-sm" value="calcular"></input>
                        </div>
                    </form>
                    <p className="lead text-muted">Solamente puede introducir 4 peliculas y 2 series hasta el momento, sin repetir ningun titulo.</p> 
                </div>
            </section>
        )    
    }
    else{
        return (
            <section className="py-3">
                <div className="container">
                    <form onSubmit={props.onAddPelicula} className="form-inline">
                        <div className="form-group">
                        <input type="text" className="form-control form-control-sm" placeholder="Nombre de la pelicula" name="titulo" required></input>
                        </div>
                        <input type="hidden" name="id" value={uid(10)}></input>                    
                        <div className="form-group">
                        <input type="submit" className="ml-2 btn btn-primary btn-sm" value="aceptar"></input>
                        </div>
                    </form>
                    <p className="lead text-muted">Solamente puede introducir 4 peliculas y 2 series hasta el momento, sin repetir ningun titulo.</p> 
                </div>
            </section>
        )           
    }
}

export default PeliculaForm