import React, { Component } from 'react'
import PeliculasList from './PeliculasList'
import PeliculaForm from './PeliculaForm'
import { url } from '../datos/token.json'
import { usuarios } from '../datos/data.json'
import UsuarioForm from './UsuarioForm'
import Ranking from './Ranking'

class App extends Component{
    constructor(...props){
        super(...props)

        this.state = {
            peliculas: [],
            metascoreTotal: 0,
            votosMax: 0,
            ratingTotal: 0,
            valor: 0,
            jugadores: [],
            btnCalcular: false,
            btnGuardar : false,
        }

        this.handleOnAddUsuario = this.handleOnAddUsuario.bind(this)
        this.handleOnAddPelicula = this.handleOnAddPelicula.bind(this)
        this.fetchData = this.fetchData.bind(this)
        this.resetData = this.resetData.bind(this)
        this.calculate = this.calculate.bind(this)

    }

    handleOnAddUsuario(e){
        e.preventDefault()

        let form = e.target

        let jugador = {
            id: form.id.value,
            nombre: form.usuario.value,
            puntos: this.state.valor,
        }

        const found = this.state.jugadores.find( element => (element.nombre === jugador.nombre) )
        if(!found){
            let jugadoresDesordenados = this.state.jugadores.concat([jugador])
            this.setState({
                jugadores: jugadoresDesordenados.sort( (a,b) => b.puntos-a.puntos ) 
            })    
        }


    }

    handleOnAddPelicula(e){
        e.preventDefault()

        if(this.state.peliculas.length < 6){
            if( this.state.peliculas.filter( elem => (elem.tipo === 'movie') ).length < 4 ||
                this.state.peliculas.filter( elem => (elem.tipo === 'series') ).length < 2 ) {

                let form = e.target

                fetch(url + form.titulo.value)
                .then( response => (response.json()))
                .then( result => {
                    if(result.Response === "True" && (Number(result.imdbRating) > 0) ){
                        let pelicula = {
                            id: form.id.value,
                            nombre: result.Title,
                            rating: result.imdbRating,
                            votos: result.imdbVotes,
                            imagen: result.Poster,
                            metascore: (isNaN(result.Metascore)) ? 0 : result.Metascore, 
                            tipo: result.Type,
                        }
    
                        const found = this.state.peliculas.find( element => (element.nombre === pelicula.nombre) )
                        if(!found){
                            let metaTotal = (pelicula.tipo === 'movie' ) ? (Number(this.state.metascoreTotal) + Number(pelicula.metascore)) : Number(this.state.metascoreTotal) 
                            this.setState({
                                peliculas: this.state.peliculas.concat([pelicula]),
                                metascoreTotal: metaTotal,
                                ratingTotal: Number(this.state.ratingTotal) + Number(pelicula.rating),
                                votosMax: (Number( pelicula.votos.replace(/\,/g,'') ) > Number(this.state.votosMax)) ? Number(pelicula.votos.replace(/\,/g,'')) : this.state.votosMax 
                            })

                            if (this.state.peliculas.length === 6){
                                this.setState({
                                    btnCalcular : true
                                })
                            }
                        }
                    }
                })

            }

        }
    }

    fetchData() { 
        usuarios.sort( (a,b) => b.puntos-a.puntos )
        this.setState({ jugadores: usuarios })
    }

    resetData() {
        this.setState( { peliculas: [] } )
    }

    componentDidMount() {
        //this.fetchData()
    }

    calculate(){
        let avgMeta = this.state.metascoreTotal / 4
        let avgRating = this.state.ratingTotal / 6
        
        let votos = 0
        this.state.peliculas.forEach(element => {
            let div = Number(element.votos.replace(/\,/g,'')) / Number(this.state.votosMax)
            votos += div
            
        });
      
        let result = avgMeta/100 + avgRating/10 + votos
        this.setState({
            valor: result,
            btnGuardar: true,
        })        
    }

    render() {
        return (
            <div>
                <main role="main">
                    <div className="row">
                        <div className="col-md-6">
                            <PeliculaForm onAddPelicula={this.handleOnAddPelicula} enablecalcular={this.state.btnCalcular} onCalcular={this.calculate} />
                        </div>
                        <div className="col-md-6">
                            <UsuarioForm enableguardar={this.state.btnGuardar} onAddUsuario={this.handleOnAddUsuario}/>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-10">
                            <PeliculasList peliculas={this.state.peliculas} />
                        </div>
                        {/* <div className="col-md-2">
                            <Ranking jugadores={this.state.jugadores}/>
                        </div> */}
                    </div>
                </main>
                
                {/* <Footer /> */}
            </div>
        )
    }

}

export default App